<template>
  <div id="hy-swiper">
    <div id="swiper" @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd">
      <slot></slot>
    </div>
    <slot name="indicator"></slot>
    <div class="indicator">
      <slot name="indicator" v-if="showIndicator && slideCount>=1">
        <div v-for="(item, index) in slideCount" class="indicator-item" :class="{active: index === currentIndex-1}"
             :key="index"></div>
      </slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Swiper",
    props: {
      //3
      interval: {
        type: Number,
        default: 3000
      },
      //
      animDuration: {
        type: Number,
        default: 200
      },
      //4/1
      moveRatio: {
        type: Number,
        default: 0.25
      },
      //
      showIndicator: {
        type: Boolean,
        default: true
      }
    },
    data: function () {
      return {
        slideCount: 0, // 
        totalWidth: 0, // swiper
        swiperStyle: {}, // swiper
        currentIndex: 1, // index
        scrolling: false, // 
      }
    },
    mounted: function () {
      this.$nextTick(() => {
        // 1.DOM, Slide
        setTimeout(() => {
          this.handleDom();

          // 2.
          this.startTimer();
        }, 500)
      })
    },
    methods: {
      /**
       * 
       */
      startTimer: function () {
        this.playTimer = window.setInterval(() => {
          this.currentIndex++;
          this.scrollContent(-this.currentIndex * this.totalWidth);
        }, this.interval)
      },
      stopTimer: function () {
        window.clearInterval(this.playTimer);
      },

      /**
       * 
       */
      scrollContent: function (currentPosition) {
        // 0.
        this.scrolling = true;

        // 1.
        this.swiperStyle.transition = 'transform ' + this.animDuration + 'ms';
        this.setTransform(currentPosition);

        // 2.
        this.checkPosition();

        // 4.
        this.scrolling = false
      },

      /**
       * 
       */
      checkPosition: function () {
        window.setTimeout(() => {
          // 1.
          this.swiperStyle.transition = '0ms';
          if (this.currentIndex >= this.slideCount + 1) {
            this.currentIndex = 1;
            this.setTransform(-this.currentIndex * this.totalWidth);
          } else if (this.currentIndex <= 0) {
            this.currentIndex = this.slideCount;
            this.setTransform(-this.currentIndex * this.totalWidth);
          }

          // 2.
          this.$emit('transitionEnd', this.currentIndex - 1);
        }, this.animDuration)
      },

      /**
       * 
       */
      setTransform: function (position) {
        this.swiperStyle.transform = `translate3d(${position}px, 0, 0)`;
        this.swiperStyle['-webkit-transform'] = `translate3d(${position}px), 0, 0`;
        this.swiperStyle['-ms-transform'] = `translate3d(${position}px), 0, 0`;
      },

      /**
       * DOM, DOMSlide
       */
      handleDom: function () {
        this.$nextTick(() => {
          // 1.
          let swiperEl = document.querySelector('#swiper');
          let slidesEls = swiperEl.getElementsByClassName('slide');

          // 2.
          this.slideCount = slidesEls.length;

          // 3.1, slide
          if (this.slideCount > 1) {
            let cloneFirst = slidesEls[0].cloneNode(true);
            let cloneLast = slidesEls[this.slideCount - 1].cloneNode(true);
            swiperEl.insertBefore(cloneLast, slidesEls[0]);
            swiperEl.appendChild(cloneFirst);
            this.totalWidth = swiperEl.offsetWidth;
            // this.totalWidth = document.documentElement.clientWidth;
            this.swiperStyle = swiperEl.style;
          }

          // 4.swiper, ()
          this.setTransform(-this.totalWidth);
        })
      },

      /**
       * 
       */
      touchStart: function (e) {
        // 1., 
        if (this.scrolling) return;

        // 2.
        this.stopTimer();

        // 3.
        this.startX = e.touches[0].pageX;
      },

      touchMove: function (e) {
        // 1.
        this.currentX = e.touches[0].pageX;
        this.distance = this.currentX - this.startX;
        let currentPosition = -this.currentIndex * this.totalWidth;
        let moveDistance = this.distance + currentPosition;
        // console.log(this.totalWidth)

        // 2.
        this.setTransform(moveDistance);
      },

      touchEnd: function (e) {
        // 1.
        let currentMove = Math.abs(this.distance);

        // 2.
        if (this.distance === 0) {
          return
        } else if (this.distance > 0 && currentMove > this.totalWidth * this.moveRatio) { // 0.5
          this.currentIndex--
        } else if (this.distance < 0 && currentMove > this.totalWidth * this.moveRatio) { // 0.5
          this.currentIndex++
        }

        // 3.
        this.scrollContent(-this.currentIndex * this.totalWidth);

        // 4.
        this.startTimer();
      },

      /**
       * , 
       */
      previous: function () {
        this.changeItem(-1);
        console.log(this.totalWidth)
      },

      next: function () {
        this.changeItem(1);
      },

      changeItem: function (num) {
        // 1.
        this.stopTimer();

        // 2.index
        this.currentIndex += num;
        this.scrollContent(-this.currentIndex * this.totalWidth);

        // 3.
        this.startTimer();
      }
    }
  }
</script>

<style scoped>
  #hy-swiper {
    overflow: hidden;
    position: relative;
  }

  #swiper {
    display: flex;
  }

  .indicator {
    display: flex;
    justify-content: center;
    position: absolute;
    width:100%;
    bottom: 8px;
  }

  .indicator-item {
    box-sizing: border-box;
    width: 8px;
    height: 8px;
    border-radius: 4px;
    background-color: #fff;
    line-height: 8px;
    text-align: center;
    font-size: 12px;
    margin: 0 5px;
  }

  .active {
    background-color: rgba(212, 62, 46, 1.0);
  }
</style>
